

.thumbnail-wrapper {
    position: relative;
    width: 100%;
  }
  
  .thumbnail-inner {
    position:  absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    text-align: center;
  }
  img.thumbnail-inner,
  iframe.thumbnail-inner {
    width: 100%;
    height: 100%;
    box-shadow: unset;
    border: unset;
  }
  .thumbnail-wrapper.ratio_1-1 {
    padding-top: 100%; /* 16:9 Aspect Ratio (divide 9 by 16 = 0.5625) */
  }
  .thumbnail-wrapper.ratio_4-3 {
    padding-top: 75%; /* 4:3 Aspect Ratio (divide 3 by 4 = 0.75) */
  }
  .thumbnail-wrapper.ratio_3-2 {
    padding-top: 66.66%; /* 3:2 Aspect Ratio (divide 2 by 3 = 0.6666)  */
  }
  .thumbnail-wrapper.ratio_8-5 {
    padding-top: 62.5%; /* 8:5 Aspect Ratio (divide 5 by 8 = 0.625) */
  }
  .thumbnail-wrapper.ratio_16-9 {
    padding-top: 56.25%; /* 16:9 Aspect Ratio (divide 9 by 16 = 0.5625) */
  }